        h2 1.2 Modelos de atencin: no afirmativos y afirmativos para la poblacin LGTBIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas. 

      .row.mb-5
        .col-lg-8.tarjeta.bgr_7.p-5.mb-3
          p.mb-0 Para revisar los modelos de atencin dirigidos a las personas de los sectores LGTBIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas, se hace necesario hacer referencia a la comunicacin en salud. Es fundamental conocer las estrategias de comunicacin para entregar informacin e influir en las decisiones de tipo individual y comunitario, con el objetivo de mejorar el estado de salud. Estos enfoques se centran #[span.color-primario en la escucha, la identificacin de las necesidades de las personas consultantes, la comprensin de su contexto y el respeto a su autonoma y libre desarrollo de la personalidad.] En este sentido, la comunicacin cobra relevancia, pues puede influir positivamente en la mejora de la salud pblica.
        .col-lg-4
          figure
            img(src="@/assets/curso/tema1/24.png", alt="alt")

      p.mb-5 La comunicacin en salud puede impactar significativamente la relacin mdico-paciente, la prevencin de enfermedades, la adherencia del individuo a las recomendaciones clnicas y la construccin de mensajes y campaas de salud pblica. Esto incluye la entrega de informacin sobre los factores protectores y los riesgos para las personas y la comunidad, es decir, acciones de tipo preventivo.

