            p.mb-0 Para que la comunicacin en salud sea efectiva, se deben tener en cuenta varios elementos, como la asertividad y el desarrollo de habilidades comunicativas. La comunicacin asertiva es la habilidad que permite a la persona escuchar de manera emptica, comprendiendo las necesidades y expectativas del paciente, y expresar su opinin personal con comentarios respetuosos, evitando en todo momento la posibilidad de que se genere un conflicto a causa de lo comunicado. 

      p.mb-5 Para que la comunicacin cumpla con los parmetros de asertividad, debe incluir los siguientes elementos:

      TabsC.color-acento-botones.mb-5
        .p-5.py-md-5(titulo="Postura corporal")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Postura corporal
              p La postura corporal debe generar confianza con la persona con la que se interacciona, logrando as transmitir inters y sinceridad.
            .col-lg-4.col-7: img(src='@/assets/curso/tema1/26.png', alt='')
        .p-5.py-md-5(titulo="Gestualidad")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Gestualidad
              p Los gestos son el medio por el cual se acompaa lo que se expresa de manera verbal. La gestualidad puede influir de manera positiva o negativa y puede llegar a distraer a la persona con la que se interacciona.
            .col-lg-4.col-7: img(src='@/assets/curso/tema1/27.png', alt='')
        .p-5.py-md-5(titulo="Articulacin")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Articulacin
              p Es la forma de pronunciar las palabras, la cadencia dentro de la oracin y el tono de voz, todos los cuales influyen en la comunicacin. Una articulacin inadecuada o de difcil entendimiento puede hacer que la otra persona se desanime en el proceso de escucha y comunicacin.
            .col-lg-4.col-7: img(src='@/assets/curso/tema1/28.png', alt='')
        .p-5.py-md-5(titulo="Reciprocidad")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Reciprocidad
              p Es de gran importancia tener presente el tiempo que se invierte en hablar y en escuchar. En la comunicacin, se debe mantener una justa medida evitando caer en monlogos o generar sentimientos de indiferencia hacia la comunicacin.
            .col-lg-4.col-7: img(src='@/assets/curso/tema1/29.png', alt='')
        .p-5.py-md-5(titulo="Localizacin")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Localizacin
              p El lugar en donde se desarrolla la comunicacin es de vital importancia, ya que este elemento puede generar un ambiente de cordialidad y comprensin o puede provocar distracciones o sentimientos de amenaza.
            .col-lg-4.col-7: img(src='@/assets/curso/tema1/30.png', alt='')

      p.mb-5 #[span.color-primario Tambin es importante mencionar que la comunicacin asertiva incluye categoras que se enmarcan en lo verbal, lo no verbal y lo paraverbal:]

      .row.mb-5
        .col-lg-4.mb-3(data-aos="fade-up")
