            h4.text-center Paraverbal
            p.mb-0.text-center Este tipo de comunicacin incluye elementos tanto de la comunicacin verbal como de la no verbal. Es importante tanto el contenido de lo que se dice como la manera en que se dice, considerando elementos como el tono de voz, la articulacin y el ritmo, entre otras variables. 
      
      p.mb-5 Para alcanzar una comunicacin asertiva, es necesario que el talento humano en salud desarrolle ciertas habilidades comunicativas que ayudarn a reforzar dicho proceso. Dentro de las habilidades comunicativas a tener en cuenta en el proceso de comprensin y transmisin de informacin, se describen las siguientes habilidades:

