    .bg-tema-1v3.mn   
      p #[span.color-primario Modelos de atencin no afirmativos (neutralidad, evitacin, segregacin, exotizacin)]

      p.mb-5 Durante la prestacin de los servicios de salud, se pueden presentar varias situaciones que desencadenan acciones de discriminacin y estigmatizacin hacia las personas LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas. Estos escenarios de discriminacin se evidencian en las barreras de acceso a los servicios de salud y en el trato inadecuado durante la prestacin de los servicios. Este tipo de trato inadecuado se encasilla en algunos modelos de atencin no afirmativos, los cuales se clasifican en:

      .tarjeta-slyder.tarjeta.p-5.mb-5
        SlyderA(tipo="b")
          .row.align-items-start
            .col-lg-7.mb-4.mb-lg-0
              h4(style="color: #0F5FFE").mb-4 Modelo de neutralidad
              p Este modelo se caracteriza por considerar que la orientacin sexual y la identidad de gnero de la persona son datos irrelevantes durante la atencin mdica, sin otorgar importancia a las particularidades y necesidades especficas de las personas LGBTQ+, aspectos cruciales para una historia clnica y una atencin adecuadas. 
            .col-lg-5
              figure
