          .row.align-items-start
            .col-lg-7.mb-4.mb-lg-0
              h4(style="color: #0F5FFE").mb-4 Modelo de evitacin
              p Este modelo se basa en evitar y eludir temas asociados con la orientacin sexual o la identidad de gnero. Como resultado, no se sabe cmo tratar adecuadamente a personas LGBTQ+ y a aquellas con orientaciones sexuales e identidades de gnero no hegemnicas.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/37.png', style="max-width:420px").mx-auto
          .row.align-items-start
            .col-lg-7.mb-4.mb-lg-0
              h4(style="color: #0F5FFE").mb-4 Modelo de segregacin
              p Este modelo trata a las personas LGBTQ+ y a aquellas con orientaciones sexuales e identidades de gnero no hegemnicas como pacientes de alto riesgo y presupone una serie de supuestos negativos relacionados con su vida personal, sexualidad y salud mental. 
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/38.png', style="max-width:420px").mx-auto
          .row.align-items-start
            .col-lg-7.mb-4.mb-lg-0
              h4(style="color: #0F5FFE").mb-4 Modelo de exotizacin
              p Este modelo crea y perpeta una serie de estereotipos sobre la poblacin LGBTQ+ y aquellas con orientaciones sexuales e identidades de gnero no hegemnicas, ridiculizando y minimizando la importancia de la orientacin sexual e identidad de gnero, tratndolas de manera trivial y como objeto de burla.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema1/39.png', style="max-width:420px").mx-auto
      
      p.mb-5 Tanto la neutralidad, la evitacin, la segregacin como la exotizacin se reflejan como parte de las barreras de calidad que afectan la oportunidad, calidez y satisfaccin durante el proceso de atencin. Algunas de las situaciones que se generan y afectan la calidad estn relacionadas con:

    .bg-tema-1v4.mn
