      p #[span.color-primario Modelo afirmativo]

      p Tener presente un modelo afirmativo implica que el talento humano en salud debe poseer un amplio conocimiento y comprensin sobre las experiencias y retos a los que se enfrentan las personas de los sectores LGTBIQ+ y con orientaciones sexuales e identidades de gnero diversas. Contar con modelos afirmativos contribuye a combatir la discriminacin, el acoso y la exclusin social, mediante el desarrollo y aplicacin del enfoque de igualdad de gnero y el enfoque diferencial.

      p.mb-5 #[span.color-primario Para adaptar los servicios a las necesidades de las personas que forman parte de los sectores LGBTIQ+, se recomienda:]

      .row.mb-5
        .col-lg-4.mb-3(data-aos="fade-up")
          .tarjeta.bgr.p-4.h-100
            figure.justify-content-center.d-flex.mb-3 
              img(src="@/assets/curso/tema1/46.svg", alt="alt").img130           
            p.mb-0.text-center Acceder o construir informacin sobre las necesidades en salud de las personas de sectores LGBTIQ+ en el territorio. Si esto no es viable, utilizar investigaciones de contextos similares a nivel nacional o internacional.
        .col-lg-4.mb-3(data-aos="fade-down")
          .tarjeta.bgr.p-4.h-100
            figure.justify-content-center.d-flex.mb-3 
              img(src="@/assets/curso/tema1/47.svg", alt="alt").img130
            p.mb-0.text-center Identificar las fortalezas y las barreras para la atencin integral a personas de los sectores LGBTIQ+ en el territorio o en las IPS correspondientes.
        .col-lg-4.mb-3(data-aos="fade-up")
          .tarjeta.bgr.p-4.h-100
            figure.justify-content-center.d-flex.mb-3 
              img(src="@/assets/curso/tema1/48.svg", alt="alt").img130
            p.mb-0.text-center Disear, implementar y evaluar los ajustes razonables (segn recursos, autonoma administrativa, etc.) para mejorar la calidad de la atencin, idealmente con la participacin de personas del sector LGBTIQ+. 

      .bg_grad-08
        .row.mb-5
          .col-lg-5
            figure
              img(src="@/assets/curso/tema1/49.svg", alt="")
          .col-lg-7.align-content-center
            p.mb-0.mt-5 La adaptacin de los servicios puede variar desde un conjunto de sensibilizaciones y capacitaciones al talento humano, hasta la creacin de una clnica especializada. Tambin puede incluir la consolidacin de un equipo interdisciplinario que opere en la misma red (por ejemplo, en hospitales universitarios) con capacidad para atender casos especialmente complejos (por ejemplo, una persona intersexual de 10 aos con complicaciones fsicas y psicolgicas por una ciruga reconstructiva realizada sin su consentimiento a temprana edad).
        
      p #[span.color-primario El modelo afirmativo debe tener impacto en varios niveles:]

