              li 
                i.fas.fa-heartbeat.txt-color-primario
                | Personal o trabajadores de la salud: En este nivel, es importante proporcionar todas las pautas bsicas para que el talento humano en salud tenga las herramientas necesarias para la atencin y el abordaje adecuado de las personas LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas.
          .row.bgr_8.tarjeta.p-4 
            .col-auto.d-none.d-md-flex
