            .col
              p.mb-0 Dentro de las recomendaciones dadas en el documento 'Lineamientos de atencin en los servicios de salud que consideran el enfoque diferencial, de gnero y no discriminacin para personas LGBTI', del Fondo de Poblacin de las Naciones Unidas - UNFPA, existen varias orientaciones para humanizar los servicios. Estos aspectos son claves como condicin de garanta del derecho a la salud.
        .col-lg-4(data-aos="fade-left")
          figure
            img(src="@/assets/curso/tema1/51.png", alt="")


    .bg_grad-09
      .row.mb-5
        .col-lg-4
