        .col-lg-8.align-content-center
          p.text-white.mb-0.mt-5 Dentro de las recomendaciones dadas en el documento 'Lineamientos de atencin en los servicios de salud que consideran el enfoque diferencial, de gnero y no discriminacin para personas LGBTI', del Fondo de Poblacin de las Naciones Unidas - UNFPA, existen varias orientaciones para humanizar los servicios. Estos aspectos son claves como condicin de garanta del derecho a la salud.
    
    p #[span.color-primario Elementos bsicos para humanizar los servicios de salud para las personas de los sectores LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas:]
    p A continuacin, se describen las principales recomendaciones a tener en cuenta para que la humanizacin en los servicios de salud sea efectiva y est basada en modelo afirmativo:  
      
    .bg-tema-1v5.mn
      .bgr_2.p-4
        .row.justify-content-around.align-items-center
