                h3.mb-1 #[span.color-verde Modelo Afirmativo]
                p.text-small.text-white A continuacin, encontrarn el documento 'Modelo afirmativo basado en humanizacin para la atencin de las personas que componen los sectores LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas'. Este documento ofrece una gua detallada con las principales recomendaciones para asegurar que la humanizacin en los servicios de salud sea efectiva y se base en un modelo afirmativo.
              .col-sm-auto
                a.boton.color-acento-botones(:href="obtenerLink('/downloads/Anexo_2.pdf')" target="_blank")
                  span Descargar
                  i.fas.fa-file-download
    
    p.mb-5 #[span.color-primario Orientaciones iniciales y generales para la atencin integral en salud de las personas LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas.]

    .row.mb-5
      .col-lg-8.tarjeta.bgr_7.p-5.mb-3(data-aos="fade-right")
        p.mb-0 Durante la prestacin de los servicios de salud, es importante contar con talento humano capacitado no solo en conceptos relacionados con la sexualidad, salud sexual y gnero, sino tambin en atencin teniendo en cuenta los enfoques y dems aspectos diferenciales. Los #[span.color-primario momentos de verdad] corresponden a la percepcin del servicio por parte del usuario. Estos reflejan la calidad del servicio ofrecido. Si en estos primeros momentos de verdad la persona LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas percibe un ambiente de hostilidad, irrespeto, burla o escasa comunicacin, esto provocar que la persona decida no hacer uso del servicio de salud requerido.
      .col-lg-4(data-aos="fade-left")
        figure
          img(src="@/assets/curso/tema1/55.png", alt="alt")
    
    p El talento humano debe contar con las competencias para entender y respetar la diversidad de las personas:

    .titulo-sexto.color-acento-contenido.mb-3
      h5 Figura 4.
      span Primeros momentos de verdad
    
    figure.mb-5(data-aos="zoom-in")
