    p.mb-5 Ya teniendo en cuenta las recomendaciones generales, es importante establecer pautas especficas durante el proceso de atencin de las personas de los sectores LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas.

    .bg-tema-1v6.mn.mb-5
      .row.justify-content-center
        .col-lg-8
          figure.mb-5(data-aos="zoom-in")
            img(src="@/assets/curso/tema1/58.svg", alt="alt")
    
    p.mb-5 Pasos para la valoracin clnica y el registro en la historia clnica:

    figure.mb-5(data-aos="zoom-out")
      img(src="@/assets/curso/tema1/59.svg", alt="alt")
    
    .bg-gray-dark.mn.mb-5
      .row 
        .col-auto.d-none.d-md-flex            
          figure
            img(src="@/assets/curso/tema1/60.svg", alt="alt")
        .col.align-content-center
          p.mb-0.text-white Se recomienda no realizar en la primera consulta los exmenes mamarios, genital y/o anal, ni salud sexual, a excepcin que las condiciones clnicas de la persona lo requieran o si la persona lo solicita o haga parte del tratamiento mdico especfico.
    
    .bg-tema-1v7.mn.mb-5
      p Adicional a las anteriores recomendaciones, es indispensable que el personal mdico y dems profesionales de la salud tengan en cuenta que la atencin integral de las personas LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas, cumplan con los siguientes elementos: 
      p.mb-5 #[span.color-primario A.] Activar la ruta de atencin integral dispuesta por el Ministerio de Salud y Proteccin Social acorde con las necesidades de la persona LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas. Las rutas que se deben activar segn necesidad son:

      .titulo-sexto.color-acento-contenido.mb-3
          h5 Tabla 1.
          span Rutas de atencin  

      .tabla-a.border-top-0.mb-5
        table
          thead(style="background-color: #3C1C74")
            tr.text-center.text-white
              th.tw Rutas de Atencin
              th.tw_1 Documentos de la ruta 
          tbody
            tr
              td Promocin y mantenimiento de la salud (obligatorio cumplimiento).
              td #[span.color-primario Rias-promocion-mantenimiento-salud]
            tr
              td.bgr_9 #[strong Materno perinatal (obligatorio cumplimiento).]
              td(rowspan="3") #[span.color-primario Rias-materno-perinatal]                 
            tr
              td.bgr_9 Orientaciones relacionadas con la atencin para la poblacin materno perinatal:
            tr
              td 
                ul.lista-ul--color
                  li 
                    li 
                      i.fas.fa-heartbeat.txt-color-primario
                      | El Plan Integral de Cuidado Primario aclara que la atencin preconcepcional debe darse por el especialista en ginecologa con el concurso de Endocrinologa y los dems profesionales pertinentes. Y en Adecuacin de la atencin para la poblacin de hombres transgnero, seala aspectos clave sobre la oferta de procedimientos con oportunidad y calidad, teniendo especial cuidado con el manejo de los datos de identidad que no pueden causar inconsistencias en el sistema de informacin que constituyan una barrera de atencin. 
                    li 
                      i.fas.fa-heartbeat.txt-color-primario
                      | Menciona que esta poblacin con frecuencia se encuentra sometida a tratamientos farmacolgicos y no farmacolgicos para trasformaciones corporales y cambio de sexo, que debern requerir de un equipo interdisciplinario para su atencin.
                    li 
                      i.fas.fa-heartbeat.txt-color-primario
                      | Disear protocolos adecuados de valoracin integral () que reconozca la afectacin a su salud por los entornos homofbicos,lesbofbicos o transfbicos, as como el estrs de las minoras sexuales dadas las violencias de que son objeto. Esto implica reconocer que en la anamnesis no se debe presuponer que todas las personas son heterosexuales, tienen familias nucleares-monogmicas, y que, por tanto, es necesario adecuar las preguntas, reconociendo y respetando la auto-identificacin de gnero del consultante. () 
                    li 
                      i.fas.fa-heartbeat.txt-color-primario
                      | Brinde orientacin para el cuidado de las personas en caso de transiciones de hombre a mujer (transexuales femeninas o mujeres transexuales) que se realizan implantes de senos, terapias hormonales con estrgenos, depilaciones del vello facial y corporal, operaciones para definir rasgos faciales femeninos y la ciruga de reasignacin genital ().
            tr
              td #[strong Cardio-cerebro-vascular.]
              td #[span.color-primario Rias-cardio-cerebro-vascular]   
            tr
              td #[strong Cncer.  En el caso de las personas trans masculinas evale y prevenga cncer de cuello de tero y mamas. Identifique la existencia de enfermedades tiroideas y explore apropiadamente.]
              td #[span.color-primario Rias-cancer]  
            tr
              td #[strong Alteraciones nutricionales.]
              td #[span.color-primario Rias-nutricional]  
            tr
              td #[strong Trastornos asociados al consumo de SPA.]
              td #[span.color-primario Rias-spa]  
            tr
              td #[strong Enfermedades infecciosas.]
              td #[span.color-primario Rias-infecciosas] 

      p.mb-5 #[span.color-primario B.] Garantizar el acceso al plan de beneficios a las personas LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas (financiado con recursos de la UPC) actualizado por el #[strong Ministerio de Salud y Proteccin Social], en torno a los servicios y tecnologas de salud. Dentro de los servicios y tecnologas a considerar, se enuncian de manera general:

      .row.mb-5
        .col-lg-6.mb-3
          figure
            img(src="@/assets/curso/tema1/61.png", alt="alt")
        .col-lg-6.mb-lg-0.mb-3(data-aos="fade-left")
          .acordion-modificado 
            AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Atencin primaria en salud")
                p.mb-0 Incluye promocin de la salud y prevencin de enfermedades, proteccin especfica y deteccin temprana.
              div(titulo="Acceso a servicios de salud")
                p.mb-0 Acceso mediante el servicio de urgencias o por servicios de consulta externa mdica, odontolgica, enfermera profesional o psicolgica (servicios puerta de entrada).
              div(titulo="Opcin de manejo teraputico")
                p.mb-0 Manejo teraputico disponible a travs de Telemedicina.
              div(titulo="Recuperacin de la salud")
                p.mb-0 Incluye atencin de urgencias, atencin ambulatoria, atencin con internacin, internacin domiciliaria, medicina y terapias alternativas  complementarias.
              div(titulo="Procedimientos en salud")
                p.mb-0 Garantiza procedimientos como analgesia, anestesia, sedacin, uso de tecnologas en salud, trasplantes, injertos, suministro de sangre y hemocomponentes, atencin en salud oral y tratamientos reconstructivos financiados con recursos de la UPC.
              div(titulo="Medicamentos")
                p.mb-0 Dispensacin de medicamentos segn indicaciones autorizadas, combinaciones de dosis fijas segn necesidad y uso de frmulas magistrales segn indicaciones mdicas. 

      .row.mb-5
        .col-lg-6.mb-lg-0.mb-3.order-2.order-lg-1(data-aos="fade-left")
          .acordion-modificado 
            AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Medicamentos de programas especiales")
                p.mb-0 Medicamentos de programas especiales para enfermedades de inters en salud pblica.
              div(titulo="Nutricin")
                p.mb-0 Sustancias y medicamentos para nutricin.
              div(titulo="Dispositivos mdicos")
                p.mb-0 Incluye lentes externos segn aplique y ayudas tcnicas financiadas con recursos de la UPC.
              div(titulo="Salud mental")
                p.mb-0 Ofrece atencin de urgencias en salud mental, psicoterapia ambulatoria para la poblacin general y para mujeres vctimas de violencia, atencin con internacin en salud mental y para mujeres vctimas de violencia. Es importante destacar que tener una identidad de gnero u orientacin sexual no hegemnica no se considera una patologa mental que requiera intervencin. Sin embargo, violencias basadas en prejuicio, discriminacin y exclusin pueden aumentar el riesgo psicosocial de las personas de los sectores LGBTIQ+.
              div(titulo="Atencin paliativa")
                p.mb-0 Proporciona cuidado paliativo integral.  
        .col-lg-6.mb-3.order-1.order-lg-2
          figure
            img(src="@/assets/curso/tema1/62.png", alt="alt")    
      
      p Para acceder de forma rpida al plan de beneficios dispuesto por el Ministerio de Salud y Proteccin Social, se recomienda consultar la pgina POS Ppuli. Esta herramienta permite consultar de manera gil, sencilla y dinmica las tecnologas, servicios y medicamentos de salud financiados o no con recursos de la Unidad de Pago por Capitacin. Puede acceder a travs del siguiente enlace: <a href="https://pospopuli.minsalud.gov.co/PospopuliWeb/paginas/home.aspx">https://pospopuli.minsalud.gov.co/PospopuliWeb/paginas/home.aspx</a>

      p #[strong Ejemplo_] Procedimiento de laboratorio financiado con recursos de la UPC. 

